<template>
  <q-page class="q-pa-md">
    <q-scroll-area style="height: 85vh; max-width: 90vw;">
      <q-card flat>
        <q-card-section>
          <div class="text-h6">Debugging Information</div>
        </q-card-section>
        <q-list>
          <q-expansion-item icon="info" label="Application Information" expand-separator switch-toggle-side>
            <q-card>
              <q-card-section>
                <q-item-label>Quasar Version: {{ quasarVersion }}</q-item-label>
                <q-item-label>Hosting Address: {{ hostingAddress }}</q-item-label>
                <q-item-label>App Mode: {{ appMode }}</q-item-label>
                <q-item-label>Release Mode: {{ releaseMode }}</q-item-label>
                <q-item-label>Debugging enabled: {{ debugState }}</q-item-label>
                <q-item-label>room API url: {{ apiURL }}</q-item-label>
                <q-item-label>service API url: {{ srvApiURL }}</q-item-label>

                </q-card-section>
            </q-card>
          </q-expansion-item>
          
          <q-expansion-item icon="code" label="Build Information" expand-separator switch-toggle-side >
            <q-card>
              <q-card-section>
                <q-item-label>Git Branch: {{ gitBranch }}</q-item-label>
                <q-item-label>Git Hash: {{ gitHash }}</q-item-label>
                <q-item-label>Build Timestamp: {{ buildTimestamp }}</q-item-label>
              </q-card-section>
            </q-card>
          </q-expansion-item>
          
          <q-expansion-item icon="computer" label="Client Information" expand-separator switch-toggle-side>
            <q-card>
              <q-card-section>
                <q-item-label>Client Platform: {{ clientPlatform }}</q-item-label>
                <q-item-label>Browser Info: {{ browserInfo }}</q-item-label>
                <q-item-label>Fullscreen: {{ isFullscreen }}</q-item-label>
                <q-item-label>Wake Lock Active: {{ wakeLockActive }}</q-item-label>
              </q-card-section>
            </q-card>
          </q-expansion-item>
        </q-list>
      </q-card>

    </q-scroll-area>
  </q-page>



</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useQuasar, Platform } from 'quasar';

const quasar = useQuasar();

const quasarVersion = ref('');
const hostingAddress = ref(window.location.host);
const gitBranch = ref(process.env.GIT_BRANCH || 'N/A');
const gitHash = ref(process.env.GIT_HASH || 'N/A');
const buildTimestamp = ref(process.env.BUILD_TIMESTAMP || 'N/A');
const clientPlatform = ref('');
const isFullscreen = ref(false);
const appMode = ref('');
const releaseMode = ref('');
const browserInfo = ref('');
const wakeLockActive = ref(false);
const debugState = ref(process.env.DEBUGGING);
const apiURL = ref(process.env.POK_API_URL);
const srvApiURL = ref(process.env.SRV_API_URL);

onMounted(() => {
  quasarVersion.value = quasar.version;
  clientPlatform.value = Platform.is;
  isFullscreen.value = 'N/A'
  browserInfo.value = `${navigator.userAgent}`;

  if(process.env.DEV) {
    releaseMode.value = 'Development';
  } else {
    releaseMode.value = 'Production';
  }

  appMode.value = process.env.MODE;

  // Check for Wake Lock status
  if ('wakeLock' in navigator) {
    wakeLockActive.value = true;  // Assuming that if the API exists, the wake lock might be active
    navigator.wakeLock.request('screen').catch(() => {
      wakeLockActive.value = false;  // If it fails, assume it's not active
    });
  } else {
    wakeLockActive.value = false;
  }
});
</script>

